.news-item:hover {
  text-decoration: none;
  box-shadow: 1px 7px 14px 0px rgb(143, 143, 167);
}

.news-item {
  border: 1px solid #496a8e;
}

.news-image {
  width: 250px;
  height: 150px;
  object-fit: cover;
}

.news-subtitle {
  color: #496a8e;
  font-weight: 400;
  font-size: 18px;
}