header {
  position: fixed;
  width: 100vw;
  z-index: 9;
  border-bottom: 1px solid #e0dede;
  background-color: #fff;
  -webkit-box-shadow: 0px 7px 18px -4px rgba(207,201,207,1);
    -moz-box-shadow: 0px 7px 18px -4px rgba(207,201,207,1);
    box-shadow: 0px 7px 18px -4px rgba(207,201,207,1);
}

.navbar {
  display: flex;
  align-items: center;
}

.logo-container {
  width: 30%;
  border-bottom: none;
}

.header-links {
  width: 70%;
}

.logo {
  height: 50px;
  width: 200px;
  object-fit: cover;
}

.nav-link {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  font-weight: 600;
  color: #1c3c67;
  text-decoration: none;
  padding: 16px 24px;
  white-space: nowrap;
}

.nav-link.active {
  color: #671a1d;
  font-weight: 600;
}

@media only screen and (max-width: 600px) {
  .logo-container ,
  .header-links {
    width: 100%;
  }

  .logo-container {
    border-bottom: 1px solid #e0dede;
  }

  .header-links {
    overflow: auto;
  }

  .logo {
    height: 40px;
  }
}