.hero-section {
  background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url("../img/covid-19-banner.png") no-repeat center/cover;
  height: calc(100vh - 60px);
  width: 100vw;
}

.hero-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.external-link-btn {
  padding: 8px 16px;
  border-radius: 24px;
  transition: all .5s;
}

.external-link-btn:hover {
  text-decoration: none;
}

.external-link-btn--light,
.external-link-btn--dark:hover {
  background-color: #f1f1f1;
  color: #0c0c0c;
  border: 1px solid #0c0c0c;
}

.external-link-btn--dark,
.external-link-btn--light:hover {
  background-color: #0c0c0c;
  color: #f1f1f1;
  border: 1px solid #f1f1f1;
}


@media only screen and (max-width: 600px) {
  .hero-section {
    height: 110vh;
  }
}

@media only screen and (max-width: 400px) {
  .hero-section {
    height: 120vh;
  }
}